import React, { useContext, useState } from "react";
import classNames from "classnames/bind";
import styles from "./DogCard.module.scss";
import { CartContext } from "../../Context/CartContext";
import { Link } from "react-router-dom";
import FavoriteIcon from "@mui/icons-material/Favorite";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
const cx = classNames.bind(styles);
export default function DogCard(props) {
  //nhận props từ DogsPage
  const {
    idDog,
    breedDog,
    descDog,
    imgDog,
    heightDog,
    weightDog,
    lifeDog,
    breedGroupDog,
  } = props;
  //---------------------------------------------------------------

  //Sử dụng như vầy để thay đổi các useState(vế sau), còn (vế đầu sử dụng cho các trang khác)
  const { addToCart, setDetail } = useContext(CartContext);
  //---------------------------------------------------------------
  //useState sau để thay đổi chữ addToCart -> you already add
  const [isAdded, setIsAdded] = useState(false);
  //---------------------------------------------------------------

  //handeClick khi nhấn vào button sẽ tạo ra một danh sách NewItems={...thông tin cần truyền ra ngoài, ( consolelog để rõ )}
  const handleClick = () => {
    setIsAdded((isAdded) => !isAdded);
    const newItems = {
      addImg: imgDog,
      addBreedDog: breedDog,
      addBreedGroupDog: breedGroupDog,
      addIdDog: idDog,
      addDescDog: descDog,
      addHeightDog: heightDog,
      addWeightDog: weightDog,
      addLifeDog: lifeDog,
    };

    // setLove((love)=>[...love])
    //Sử dụng destructuring để hiện thi thêm các object khác
    addToCart((item) => [...item, newItems]);
    //Set total như sau để cộng dồn số ( total += Number )
  };
  //---------------------------------------------------------------

  //detailClick khi nhấn vào card sẽ truyền ra một danh sách newItem2 ={thông tin chó cần truyền ra ngoài}
  const detailClick = () => {
    const newItems2 = {
      addIdDog: idDog,
      addBreedDog: breedDog,
      addImg: imgDog,
      addDescDog: descDog,
      addHeightDog: heightDog,
      addWeightDog: weightDog,
      addLifeDog: lifeDog,
      addBreedGroupDog: breedGroupDog,
    };
    //Thêm chỗ chứa ( chỗ chứ là setDetail ) bằng với danh sách vừa tạo. Nhớ là biến thêm [ ] để thể hiện một mảng
    setDetail([newItems2]);
  };
  return (
    <div className={cx("card")}>
      {/* Dùng Link để di chuyển tới path bên App.js */}
      <Link onClick={detailClick} to="/dogdetail">
        <div className={cx("top_card")}>
          <div className={cx("image_card")}>
            <img
              style={{ objectFit: "fill" }}
              className={cx("img")}
              src={imgDog}
              alt=""
            ></img>
          </div>
          <div style={{ width: "300px" }} className={cx("right_top_card")}>
            <h2 style={{ textTransform: "capitalize" }}>{breedDog}</h2>
          </div>
        </div>
        <div style={{ marginBottom: "20px" }} className={cx("mid_card")}>
          <h4>{descDog}</h4>
        </div>
      </Link>
      {/* Nếu isAdded = true thì hiện you already add it, = false ( mặc định ban đầu của useState ) thì hiện Add to cart */}
      {isAdded ? (
        <button className={cx("add-cart")} >
          <FavoriteIcon></FavoriteIcon>
        </button>
      ) : (
        <button  className={cx("add-cart")} onClick={handleClick}>
          <FavoriteBorderIcon></FavoriteBorderIcon>
        </button>
      )}
    </div>
  );
}
